import * as React from "react"
import Slider from "react-slick"
import { Button } from "semantic-ui-react"
import { ScreenSize, withScreenSize } from "./withScreenSize"

export const Carousel = ({ items, screenSize } : Props) => {
    let slideCount, showArrow
    if (screenSize === "mobile") {
      slideCount = 1
      showArrow = false
    } else if (screenSize === "tablet") {
      slideCount = 2
      showArrow = false
    } else {
      slideCount = 3
      showArrow = true
    }

    return (
      <Slider
        slidesToShow={slideCount}
        arrows={showArrow}
        nextArrow={<div><Button icon={"angle right"} circular/></div>}
        prevArrow={<div><Button icon={"angle left"} circular/></div>}
        dots={true}
        infinite={false}>
        {
          items.map(item =>
            (
              <div className={"slide"}>
                {item}
              </div>
            )
          )
        }
      </Slider>
    )
  }


interface Props {
  items: Array<React.ReactNode>
  screenSize: ScreenSize
}

export default withScreenSize(Carousel)
